<template>
	<div id="auth-forget">
		<public-header title="忘记密码"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-field
					v-model="mobile"
					label="手机号"
					placeholder="请输入手机号"
				/>
				<van-field
					v-model="captcha"
					type="number"
					label="图形验证码"
					placeholder="请输入图形验证码"
				/>
				<van-field
					v-model="code"
					center
					clearable
					label="手机验证码"
					placeholder="请输入6位短信验证码"
				>
					<van-button slot="button" size="small" class="bg-theme" @click="onGetCode" :disabled="!getCode">{{codeText}}</van-button>
				</van-field>
				<van-field
					v-model="password"
					label="新密码"
					placeholder="请输入您的登录密码"
					type="password"
				/>
				<van-field
					v-model="repassword"
					label="确认新密码"
					placeholder="请确认您的登录密码"
					type="password"
				/>
			</van-cell-group>
			<div class="email-img" @click="onRefreshCaptcha">
				<img :src="captcha_img" alt="">
			</div>
			<div class="padding-15">
				<van-button size="normal" block class="bg-theme font-16" :disabled="!islegal" @click="onSubmit">立即找回</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				getCode: true,
				codeText: '发送验证码',
				downcount: 60,
				islegal: true,
				mobile: '',
				code: '',
				password: '',
				repassword: '',
				captcha: '',
				captcha_img: '',
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				this.onRefreshCaptcha();
				this.showPreLoading = false;
			},
			onGetCode() {
				var that = this;
				if(!that.getCode) {
					return false;
				}
				if(!that.mobile) {
					that.util.$toast('手机号不能为空');
					return false;
				}
				if(!that.util.isValidMobile(that.mobile)) {
					that.util.$toast('手机号格式错误');
					return false;
				}
				if(!this.captcha) {
					this.$toast('请输入图形验证码');
					return false;
				}
				getData({
					vue: that,
					url: 'system/common/code',
					method: 'POST',
					data: {
						mobile: that.mobile,
						captcha: that.captcha
					},
					success: (res) => {
						that.getCode = false;
						that.codeText = that.downcount + "秒后重新获取";
						var timer = setInterval(function(){
							that.downcount--;
							if(that.downcount <= 0){
								clearInterval(timer);
								that.codeText = "获取验证码";
								that.downcount = 60;
								that.getCode = true;
							} else {
								that.codeText = that.downcount + "秒后重新获取";
							}
						}, 1000);
						that.util.$toast('验证码发送成功, 请注意查收');
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.mobile){
					that.util.$toast('手机号不能为空');
					return false;
				}
				if(!that.util.isValidMobile(that.mobile)) {
					that.util.$toast('手机号格式错误');
					return false;
				}
				if(!that.code) {
					that.util.$toast('请输入短信验证码');
					return false;
				}
				if (!that.password) {
					that.util.$toast('密码不能为空');
					return false;
				} else {
					var length = that.password.length;
					if(length < 8 || length > 20) {
						that.util.$toast("请输入8-20位密码");
						return false;
					}
					var reg = /[0-9]+[a-zA-Z]+[0-9a-zA-Z]*|[a-zA-Z]+[0-9]+[0-9a-zA-Z]*/;
					if(!reg.test(that.password)) {
						that.util.$toast("密码必须由数字和字母组合");
						return false;
					}
				}
				if (!that.repassword) {
					that.util.$toast('请重复输入密码');
					return false;
				}
				if(that.password != that.repassword) {
					that.util.$toast('两次密码输入不一致');
					return false;
				}
				var params = {
					mobile: that.mobile,
					code: that.code,
					password: that.password,
					repassword: that.repassword,
				}
				setData({
					vue: that,
					url: 'delivery/auth/forget/index',
					data: params,
					message: '找回成功',
					redirect: that.util.getUrl({path: '/pages/auth/login'})
				});
			},
			onRefreshCaptcha() {
				this.util.request({
					url: 'delivery/auth/forget/captcha',
				}).then((res) => {
					this.captcha_img = res.data.message.message.captcha;
				})
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style scoped>
	#auth-forget .email-img{
		height: 32px;
		width: 90px;
		position: absolute;
		top: 59px;
		right: 15px;
	}
	#auth-forget .email-img img{
		border: none;
		width: 100%;
		height: 100%;
	}
</style>


